  androidTalkClient: t('setting', 'Nextcloud Talk for Android'),
  davDroid: 'DAVdroid',
  webPirate: 'WebPirate',
  sailfishBrowser: 'SailfishBrowser'
};
var iconMap = {
  ie: 'icon-desktop',
  edge: 'icon-desktop',
  firefox: 'icon-desktop',
  chrome: 'icon-desktop',
  safari: 'icon-desktop',
  androidChrome: 'icon-phone',
  iphone: 'icon-phone',
  ipad: 'icon-tablet',
  iosClient: 'icon-phone',
